.text{
    height: 60px;
    width: 100%;
}
/* Position and sizing of burger button */
.bm-burger-button {
    top: 10px;
    width: 36px;
    height: 36px;
    position: absolute;
    margin-left: 20px;
  }
  
  /* Color/shape of burger icon bars */
  .bm-burger-bars {
    width: 36px;
    background-image: linear-gradient(to right,#E55D87, #3399ff);
  }
  
  /* Color/shape of burger icon bars on hover*/
  .bm-burger-bars-hover {
    background-image: linear-gradient(to right,#E55D87, #3399ff);
  }
  
  /* Position and sizing of clickable cross button */
  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
  
  /* Color/shape of close button cross */
  .bm-cross {
    background: #bdc3c7;
  }
  
  /*
  Sidebar wrapper styles
  Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
  */
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
    background-image: linear-gradient(to right,#E55D87, #3399ff);
  }
  
  /* General sidebar styles */
  .bm-menu {
    background-image: linear-gradient(to right,#E55D87, #3399ff);
    padding: 2.5em 1.5em 2.5em;
    font-size: 1.15em;
  }
  
  /* Morph shape necessary with bubble or elastic */
  .bm-morph-shape {
    fill: #882088;
  }
  
  /* Wrapper for item list */
  .bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
  }
  
  /* Individual item */
  .bm-item {
    color: #d1d1d1;
    margin-bottom: 10px;
    text-align: left;
    text-decoration: none;
    transition: color 0.2s;
  }
  
  .bm-item:hover {
    color: #ffffff;
  }
  /* Styling of overlay */
  .bm-overlay {
    background: rgba(175, 15, 135, 0.3);
  }